.Button {
  width: 200px;
  height: 50px;
  background-color: rgb(155, 155, 155);
  cursor: pointer;
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 24px;
}

.Button.clear {
  background-color: palevioletred;
}
.Button.toggled {
  background-color: blueviolet;
}

.DateView {
  visibility: hidden;
  font-size: 32px;
  margin-left: 10px;
  margin-right: 10px;
  width: 200px;
  text-align: center
}

.DateView.visible {
  visibility: visible;
}

.Container {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 40px;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Container.row {
  -ms-flex-direction: row;
      flex-direction: row;
}


